import Vue from 'vue'
import './plugins/axios'
import App from './App.vue'
import router from './router'
import store from './store'

import 'lib-flexible/flexible'
// 引入vuex
// import store from './store/index'
// 引入cookie
import { setCookie, getCookie, delCookie } from './assets/js/cookie'
// 全局注入cookie的设置获取删除方法
Vue.prototype.$cookie = {
    setCookie,
    getCookie,
    delCookie
}



import 'vant/lib/index.less'

// 首页
import './assets/js/index/index'

// 分类页面
import './assets/js/sort/sort1'
// 登录
import './assets/js/login/index'
// 商品详情
import './assets/js/detail/index'
// 搜索
import './assets/js/detail/index'


//从vant导入组件
import {
  Tabbar,
  TabbarItem,
  Search,
  Toast,
  Swipe,
  SwipeItem,
  Lazyload,
  Tab,
  Tabs,
  Icon,
  Grid,
  GridItem,
  Popup,
  Cell,
  CellGroup,
  Tag,
  Image as VanImage,
  Divider,
  Loading,

  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  RadioGroup,
  Radio,
  Stepper,
  Checkbox,
  CheckboxGroup,
  AddressEdit,
  Dialog,
  ActionSheet,
  SubmitBar,
  Step,
  Steps,
  Collapse,
  CollapseItem
} from 'vant';

Vue
  .use(RadioGroup)
  .use(Radio)
  .use(GoodsAction)
  .use(GoodsActionIcon)
  .use(GoodsActionButton)
  .use(Stepper)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(AddressEdit)
  .use(Dialog)
  .use(ActionSheet)
  .use(SubmitBar)
  .use(Step)
  .use(Steps)
  .use(Collapse)
  .use(CollapseItem)

  .use(Tabbar)
  .use(TabbarItem)
  .use(Search)
  .use(Toast)
  // 轮播
  .use(Swipe)
  .use(SwipeItem)
  .use(Lazyload)
  .use(Tab)
  .use(Tabs)
  .use(Icon)
  .use(Grid)
  .use(GridItem)
  .use(Popup)
  .use(Cell)
  .use(CellGroup)
  .use(Tag)
  .use(VanImage)
  .use(Divider)
  .use(Loading)


Vue.config.productionTip = false
import { mapState } from 'vuex'
new Vue({
    computed: {
        ...mapState({
            userInfo: "userInfo"
        })
    },
    watch: {},
    created() {
        // console.log("ss", this.userInfo);
    },
    router,
    store,
    render: h => h(App)
}).$mount('#app')