import Vue from 'vue'
import VueRouter from 'vue-router'

import {routes} from './routes'
// 引入vuex
import state from '../store/index'

// 处理重定向的抛出错误问题
const a = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
    return a.call(this, location).catch(err => err);
}
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
});
// 全局路由守卫
router.beforeEach((to, from, next) => {
  // 当前时间戳
  let time = new Date().getTime();
  // 当前的时间戳大于token过期的时间戳, 表示token过期
  if (Vue.prototype.$cookie.getCookie("token")) {
      if (time >= Number(Vue.prototype.$cookie.getCookie("time"))) {
          //删除token和时间戳，重新登录
          Vue.prototype.$cookie.delCookie("token");
          Vue.prototype.$cookie.delCookie("time");
          next('/login');
      }
  }
  // 需要登录才能进去的路由
  if (Vue.prototype.$cookie.getCookie("token")) {
      // 把cookie里面的token赋给全局的token
      state.state.token = Vue.prototype.$cookie.getCookie("token");
      // 获取用户信息
      state.dispatch("getUser");
  }
  // 当页面需要登录并且cookie里面的token不存在时就跳转到登录页面
  if (to.meta.needLogin && !Vue.prototype.$cookie.getCookie("token")) {
      next("/login");
      return;
  }
  next();

})

export default router
