export const routes = [
  {
    path: '/',
    name: 'Main',
    component: () => import('../views/Main.vue'),
    children: [
      {
        path: 'home',
        name: 'Home',
        alias: '',
        component: () => import('../views/Home.vue'),
      },
      // 曲目
      {
        path: 'classify',
        name: 'Classify',
        component: () => import('../views/Classify.vue'),
      },
      {
        path: 'cart',
        name: 'Cart',
        component: () => import('../views/Cart.vue'),
        meta: { paddingFlag: true, needLogin: true } //需要登录
      },
      {
        path: 'my',
        name: 'My',
        component: () => import('../views/My.vue'),
        meta: { paddingFlag: true, needLogin: true } //需要登录
      },
      {
        path: 'sort',
        name: 'Sort',
        component: () => import('../views/Sort.vue'),
      },
      {
        path: 'coupons',
        name: 'Coupons',
        component: () => import('../views/Coupons.vue'),
      },
      {
        path: 'collection',
        name: 'Collection',
        component: () => import('../views/Collection.vue'),
      },
      {
        path: 'address',
        name: 'Address',
        component: () => import('../views/Address.vue'),
      },
      {
        path: 'order',
        name: 'Order',
        component: () => import('../views/Order.vue'),
      },
      // {
      //   path: 'products',
      //   name: 'Products',
      //   component: () => import('../views/Products.vue'),
      // },
      {
        // 订单
        path: '/order/list/:id',
        name: 'Order',
        component: () =>
            import ('../views/Order.vue'),
        meta: { paddingFlag: false, needLogin: true } //需要登录
    },
      {
        path: 'login',
        name: 'Login',
        component: () => import('../views/Login.vue'),
      },
      {
        path: 'mine',
        name: 'Mine',
        component: () => import('../views/Mine.vue'),
        meta: { paddingFlag: false, needLogin: true } //需要登录
      },
      {
        // 商品提交订单  点击立即购买按钮之后
        path: '/order/submit/:id',
        name: 'Submit',
        component: () =>
            import ('../views/Submit.vue'),
        meta: { paddingFlag: false }
      },
      {
        // 订单状态，点击立即结算之后,订单的成功还是失败
        path:'/order/status/:id',
        name:"OrderStatus",
        component:()=>import('../views/OrderStatus.vue')
    },
    {
        // 订单的详细详细
        path:'/order/detail/:id',
        name:'OrderDetail',
        component:()=>import('../views/OrderDetail.vue')
    },
    {
      path: '/search',
      name: 'Search',
      component: () =>
          import ('../views/Search.vue'),
      meta: { paddingFlag: false }
    },
    {
      // 已退款
      path: '/order/refund_list',
      name: 'Refund',
      component: () =>
          import ('../views/OrderRefund.vue')
  },
    {
      // 我的设置
      path: '/mine/set',
      name: 'Set',
      component: () =>
          import ('../views/Set.vue')
    },
    {
      // 快速选择下面的商品点击
      path: '/goods_list',
      name: 'GoodsList',
      component: () =>
          import ('../views/Products.vue')
    },
    {
      // 商品详情
      path: '/detail/:id',
      name: 'Detail',
      component: () =>
          import ('../views/Detail.vue')
    }
    ]
  }
]